* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #100606;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bar {
  margin-left: 2rem;
  font-size: 2rem;
  background-color: none;
}

.nav-menu {
  background-color: #100606;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-self: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #01bf71;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #100606;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: start;
  align-items: center;
}


span {
  margin-left: 16px;
}

.home2,
.reports,
.products {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}
